/* Contenedor principal */
.loadingTodo-container {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.1);
    border-radius: 12px;
    padding: 16px;
    background-color: #f7f7f7;
    overflow: hidden;
    width: 100%;
    max-width: 400px;
    height: 60px;
    background: linear-gradient(90deg, rgba(250, 250, 250, 0.8), rgba(230, 230, 230, 0.6));
    animation: pulse 1.5s infinite ease-in-out;
  }
  
  /* Efecto de animación para el contenedor */
  @keyframes pulse {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
  
  /* Estilo del icono de completar */
  .loadingTodo-completeIcon,
  .loadingTodo-deleteIcon {
    cursor: not-allowed;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    background-color: #e0e0e0;
    animation: loading 1.5s infinite ease-in-out;
  }
  
  /* Animación de los iconos */
  @keyframes loading {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.1);
    }
    100% {
      transform: scale(1);
    }
  }
  
  /* Estilo del texto de carga */
  .loadingTodo-text {
    width: 200px;
    height: 18px;
    background-color: #e0e0e0;
    border-radius: 4px;
    animation: loadingText 1.5s infinite ease-in-out;
  }
  
  /* Animación para el texto */
  @keyframes loadingText {
    0% {
      width: 200px;
      opacity: 0.7;
    }
    50% {
      width: 220px;
      opacity: 0.5;
    }
    100% {
      width: 200px;
      opacity: 0.7;
    }
  }
  