/* Estilo base para la ventana */
body {
    font-family: Arial, sans-serif;
    background-color: #f4f4f4;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: 0;
}

/* Fondo semi-transparente para la ventana emergente */
.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6); /* Fondo oscuro semi-transparente */
    display: none; /* Inicialmente oculto */
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

/* Estilo de la ventana de felicitaciones */
.popup-content {
    background-color: #fff;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    width: 300px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

/* Título */
.popup-content h2 {
    color: #28a745; /* Verde de felicitaciones */
    font-size: 2em;
    margin-bottom: 15px;
}

/* Mensaje */
.popup-content p {
    color: #555;
    font-size: 1.1em;
}

/* Botón de cerrar */
.close-btn {
    background-color: #28a745;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 1.1em;
    cursor: pointer;
    margin-top: 20px;
}

.close-btn:hover {
    background-color: #218838;
}
