/* Estilo general del modal */
.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);
  display: flex;
  justify-content: center;
  align-items: center;
  animation: fadeIn 0.5s ease; /* Animación de entrada */
}

.modal.show {
  display: flex;  /* Mostrar el modal */
}

.modal.hide {
  animation: fadeOut 0.5s ease forwards; /* Animación de salida */
}

/* Fuente y estilo avanzado */
h2 {
  font-family: 'Roboto', sans-serif; /* Fuente moderna y profesional */
  font-size: 28px; /* Un tamaño adecuado para que el título se vea prominente */
  font-weight: 600; /* Peso fuerte para darle importancia */
  color: #1a202c; /* Color oscuro para mantener la seriedad */
  text-align: center;
  letter-spacing: 1px; /* Espaciado moderado entre letras para un toque de sofisticación */
  text-transform: capitalize; /* La primera letra de cada palabra será mayúscula */
  margin: 0;
  padding-bottom: 15px;
  position: relative;
  transition: all 0.4s ease;
  display: inline-block;
}

/* Efecto de sombra sutil para crear profundidad */
h2::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, #3498db, #2ecc71); /* Gradiente suave */
  filter: blur(6px); /* Efecto borroso para darle un fondo difuso */
  z-index: -1; /* Poner la sombra detrás del texto */
  border-radius: 10px; /* Bordes redondeados para suavizar */
  transition: all 0.4s ease; /* Suaviza la transición del gradiente */
}

/* Efecto de animación al pasar el mouse (hover) */
h2:hover {
  color: #fff; /* Cambia el color del texto al blanco cuando el cursor está sobre él */
  text-shadow: 0 4px 6px rgba(0, 0, 0, 0.2), 0 1px 2px rgba(0, 0, 0, 0.1); /* Sombras para el texto */
  transform: scale(1.05); /* Le da un leve aumento de tamaño al pasar el mouse */
}

/* Animación de entrada suave */
@keyframes fadeInTitle {
  0% {
    opacity: 0;
    transform: translateY(-30px); /* El título empieza desde arriba */
  }
  100% {
    opacity: 1;
    transform: translateY(0); /* El título entra en su lugar */
  }
}

/* Aplicando la animación de entrada al título */
h2 {
  animation: fadeInTitle 0.6s ease-out forwards; /* El título entra suavemente */
}

/* Adaptación responsiva */
@media (max-width: 768px) {
  h2 {
    font-size: 24px; /* Reducir el tamaño de la fuente en pantallas más pequeñas */
    letter-spacing: 0.5px; /* Menos espaciado entre letras */
  }
}



/* Animación de entrada (fade in + slide down) */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-50px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Animación de salida (fade out + slide up) */
@keyframes fadeOut {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(-50px);
  }
}

/* Contenido del modal */
.modal-content {
  background: #f1f1f1; /* Fondo sofisticado */
  margin: 10% auto;
  padding: 30px;
  border-radius: 15px;
  width: 60%;
  max-width: 380px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  position: relative;
  text-align: center;
}

@keyframes slideDown {
  from {
    transform: translateY(-200px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* Estilo de cierre (X) */
.close {
  color: #ff5f6d;
  font-size: 25px;
  font-weight: bold;
  position: absolute;
  top: 15px;
  right: 20px;
  cursor: pointer;
  transition: color 0.3s ease;
}

.close:hover {
  color: #ef32d9;
}

/* Estilo de los botones */
.btn-action, .btn-accept {
  background: linear-gradient(135deg, #36d1dc, #5b86e5);
  color: white;
  border: none;
  padding: 12px 24px;
  font-size: 16px;
  font-weight: bold;
  border-radius: 30px;
  margin: 12px 0;
  cursor: pointer;
  transition: transform 0.3s ease, background-color 0.3s ease;
}

.btn-action:hover, .btn-accept:hover {
  transform: scale(1.1);
  background: linear-gradient(135deg, #5b86e5, #36d1dc);
}

.btn-secondary {
  background: linear-gradient(135deg, #ff5f6d, #ffc371);
  color: white;
  border: none;
  padding: 12px 24px;
  font-size: 16px;
  font-weight: bold;
  border-radius: 30px;
  margin: 12px 0;
  cursor: pointer;
  transition: transform 0.3s ease, background-color 0.3s ease;
}

.btn-secondary:hover {
  transform: scale(1.1);
  background: linear-gradient(135deg, #ffc371, #ff5f6d);
}

/* Estilo del campo de entrada */
input {
  width: 100%;
  padding: 12px;
  margin: 15px 0;
  border-radius: 10px;
  border: 2px solid #ddd;
  font-size: 16px;
  transition: border-color 0.3s ease;
}

input:focus {
  outline: none;
  border-color: #5b86e5;
  box-shadow: 0 0 8px #5b86e5;
}
input#newTodo {
  font-family: Georgia, 'Times New Roman', Times, serif;  /* Cambia el tipo de fuente */
  font-size: 18px;                   /* Cambia el tamaño del texto */
  padding: 10px;                     /* Agrega un poco de espacio dentro del campo */
  border-radius: 5px;                /* Bordes redondeados */
  border: 2px solid #ddd;            /* Borde gris claro */
  width: 100%;                       /* El campo de entrada ocupa todo el ancho disponible */
  box-sizing: border-box;            /* Incluye el padding en el tamaño total */
  text-transform: capitalize;
}

input#newTodo:focus {
  outline: none;                     /* Elimina el borde azul predeterminado */
  border-color: #5b86e5;             /* Cambia el color del borde al enfocarse */
  box-shadow: 0 0 8px #5b86e5;       /* Agrega un sombreado sutil */
}


/* Contenedor de botones */
.button-group {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  margin-top: 20px;
}

.button-group .btn-action, .button-group .btn-secondary {
  width: 48%;
}

.btn-accept {
  width: 100%;
  background: linear-gradient(135deg, #11998e, #38ef7d);
  color: white;
  border: none;
  padding: 12px 24px;
  font-size: 16px;
  font-weight: bold;
  border-radius: 30px;
  cursor: pointer;
  transition: transform 0.3s ease, background-color 0.3s ease;
}

.btn-accept:hover {
  transform: scale(1.1);
  background: linear-gradient(135deg, #38ef7d, #11998e);
}

/* Responsividad */
@media (max-width: 1024px) {
  .modal-content {
    width: 70%;
  }

  .button-group .btn-action, .button-group .btn-secondary {
    width: 48%;
  }

  .btn-accept {
    width: 100%;
  }

  input {
    width: 100%;
    padding: 10px;
    font-size: 15px;
  }
}

@media (max-width: 768px) {
  .modal-content {
    width: 80%;
    margin: 5% auto;
  }

  .button-group .btn-action, .button-group .btn-secondary {
    width: 100%;
    margin: 8px 0;
  }

  .btn-accept {
    width: 100%;
  }

  input {
    width: 100%;
    padding: 10px;
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  .modal-content {
    width: 90%;
    padding: 20px;
  }

  .btn-action, .btn-accept, .btn-secondary {
    padding: 12px 18px;
  }

  input {
    width: 100%;
    padding: 8px;
    font-size: 14px;
  }
}
